/* Home.js */
.container {
    height: 100%;
    position: relative;
  }
.load{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}
.loader {
  width: 80px;
  height: 80px;
  border: 3px solid rgba(255,255,255,.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}
@-webkit-keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}

.logo{
  width: 200px;
  height: 50px;
  padding: 10px 0;
  margin: 0 0 0 20px;
}
.logo > img{
  width: 100%;
  height: 100%;
}
.movie__box{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  border-top:1px solid rgba(255, 255, 255, 0.4) ;
  padding-top: 20px;
}

