.movie{
    width: 15%;  
    position: relative;
    margin-bottom: 20px;
}
.movie:hover .text__hover {
    display: block;
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    line-height: 1.5;
}
.movie:hover .movie__img{
    filter: brightness(0.5);
}
.movie__img__wrap{
   position: relative;
   width: 100%;
}

.movie__img {
    position: relative;
    width: 100%;
}

.title__wrap{

}

.movie__title{
    font-family: 'Happiness-Sans-Title';
    font-size: 20px;
}

.movie__title>span{
    font-size: 16px;
    color: #eee;
}
.rating{
    font-size: 30px;
    text-align: center;    
}
.movie__summary{
    padding: 10px;
}
.text__hover{
    display: none;
}
.detail__btn{
    border: none;
    border-radius: 5px;
    background: #ec0101;
    width: 100%;
    height: 45px;
    cursor: pointer;
    margin: 5px 0;
}
.detail__btn:hover{
    box-shadow: inset 1px 1px 10px 1px #333;
}

.detail__btn a{
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    font-weight: bold;

}
.movie__genres{
    color: #000;
    padding: 0;
    display: flex;
    gap: 5px;
}
.movie__genres>li{
    background: rgba(255, 255, 255, 0.5);
    color: #fff;
    width: 100px;
    text-align: center;
    box-sizing: border-box;
    padding: 5px;
    border-radius: 30px;
    font-size: 13px;
}

/* DetailMovie.js */

